import React, { FC } from 'react';
import { useSelector } from 'react-redux';
import {
  ColumnsProps,
  decimalFormatter,
  DefaultSortProps,
  FilterType,
  getExtraData,
  OrderType,
  SearchDataGrid,
  SearchViewProps,
  WindowComponent,
} from 'summer';
import { hasEmpresa } from '../../../../dependencies/empresaDependency';
import { hasEmpresaMFP } from '../../../../dependencies/empresaMFPDependency';
import { CLASS_NAME, ValeTransporte } from '../../../../models/dtos/mfp/valeTransporte';
import { Reducers } from '../../../../store/ducks';
import { GlobalState } from '../../../../store/ducks/global.duck';

const defaultSorted: DefaultSortProps[] = [
  {
    dataField: 'codigo',
    order: OrderType.ASC,
  },
];

const ValeTransporteSearchView: FC<SearchViewProps<ValeTransporte>> = props => {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);

  const columns: ColumnsProps[] = [
    {
      dataField: 'codigo',
      filter: {
        type: FilterType.NUMBER,
      },
      sort: true,
      text: 'Código',
    },
    {
      dataField: 'descricao',
      sort: true,
      text: 'Descrição',
    },
    {
      dataField: 'valorUnitario',
      formatter: decimalFormatter,
      sort: true,
      text: 'Valor Unitário',
    },
    {
      dataField: 'forcli.razaoSocial',
      sort: true,
      text: 'Fornecedor (Razão Social)',
    },
  ];

  return (
    <WindowComponent
      title="Manutenção de Tarifas"
      useGlobalParameter={true}
      globalParameter={globalReducer.globalParameter}
      dependencies={[() => hasEmpresa(globalReducer.globalParameter), () => hasEmpresaMFP(globalReducer.globalParameter)]}
      wikiId={1454}
    >
      <SearchDataGrid
        {...props}
        {...props.searchDataGridProps}
        columns={columns}
        defaultSorted={defaultSorted}
        dtoClassName={CLASS_NAME}
        extraData={getExtraData(props, 'ValeTransporteEditView', CLASS_NAME)}
      />
    </WindowComponent>
  );
};

export default ValeTransporteSearchView;
